import { createWebHistory, createRouter } from "vue-router";

const routes = [

  {
    path: "/lesson",
    name: "lesson",
    component: () => import("@/pages/lesson.vue"),
  },
  {
    path: "/",
    name: "blog",
    component: () => import("@/pages/Blog.vue"),
  },
  {
    path: "/detail/:idx(\\d+)",
    name: "detail",
    component: () => import("@/pages/Detail.vue"),
    beforeEnter: (to, from)=>{ // Navigation guards
      // 페이지에 입장하기 전에 로그인 확인하는 용도로도 쓸 수 있고 다양한 용도로 쓸 수 있을듯...?
      // to와 from 인자는 안필요하면 안넣어도 됨
      console.log(to)
      console.log(from)
      
      if (!window.location.href.includes('/detail/')) {
        return alert('비정상적인 접근')
        // return false를 넣으면 라우팅 중단이 일어남!!!! 페이지 이동 막으려면 false 사용
      }
    },
    children:[ //nested routes
    // 자식을 품고있는 라우팅 기능같은 느낌
      {
        path: "comment",
        name: "comment",
        component: () => import("@/pages/Comment.vue"),
      }
    ],
  },
  
];

const router = createRouter({
  history: createWebHistory(),
  routes,
});

router.beforeEach((to, from) => {
  // 위의 Navigation guards를 다수의 페이지에 적용
  //페이지 변경 전에 실행할 코드
  // 전체 적용해도 되고, if(to.fullPath === '') 으로 특정 페이지들만 적용 가능
})

/**
 * 
 * beforeRouteEnter(){}
  beforeRouteUpdate(){} 
  vue 페이지 내에서도 사용가능

  물론 인자에 to, from 넣을 수 있음
 * 
 */


export default router;
