<template>
  <div>
    <NavBar />
    <div class="container mt-5">
      <router-link to="/">HOME</router-link>
      <router-link to="/">Blog</router-link>
      <h4>Title</h4>
      <p>made with VUE</p>
    </div>
    <router-view :post="post" />
  </div>
</template>

<script>
import NavBar from '@/components/NavBar.vue'
import axios from 'axios'

export default {
  name: 'App',
  data(){
    return {
      post:[],
    }
  },
  components: {
    NavBar,
  },
  created(){

  },
  mounted(){
    axios.get('/data/blog/post.json')
    .then(res=>{
      console.log(res.data)
      this.post = res.data


    }).catch(er=> {
      console.log(er)
    })
  },
  unmounted(){

  },
}
</script>

